<template>
  <!--  -->
  <div class="comnum">
    <Detail />

    <!--  -->
    <el-form :inline="true" class="form">
      <el-form-item>
        <el-tooltip content="2" placement="left">
          <el-checkbox-group @change="focus" v-model="checkedList" :max="2">
            <el-checkbox-button label=""></el-checkbox-button>
            <el-checkbox-button label=""></el-checkbox-button>
            <el-checkbox-button label=""></el-checkbox-button>
            <el-checkbox-button label=""></el-checkbox-button>
          </el-checkbox-group>
        </el-tooltip>
      </el-form-item>
      <el-form-item>
        <el-tooltip
          
          placement="top"
        >
         <div slot="content" > {{tip}} <br/></div>
          <el-input
            v-model.trim="heNum"
            ref="ipt1"
            placeholder=""
            clearable
            @keyup.enter.native="$refs.ipt2.focus()"
          ></el-input>
        </el-tooltip>
      </el-form-item>

      <el-form-item>
        <el-tooltip content="" placement="top">
          <el-input-number
            controls-position="right"
            v-model="money"
            placeholder=""
            ref="ipt2"
            :min="0"
            @focus="$refs.ipt2.select()"
            @keyup.enter.native="process"
          ></el-input-number>
        </el-tooltip>
      </el-form-item>

      <el-form-item>
        <el-button icon="el-icon-sold-out" @click="process" type="primary"
          ></el-button
        >
      </el-form-item>
    </el-form>

    <!--  -->
    <el-form :inline="true" class="form">
      <el-form-item>
        <el-tooltip content="1" placement="left">
          <el-radio-group @input="focus_2" v-model="radio">
            <el-radio-button label=""></el-radio-button>
            <el-radio-button label=""></el-radio-button>
            <el-radio-button label=""></el-radio-button>
            <el-radio-button label=""></el-radio-button>
          </el-radio-group>
        </el-tooltip>
      </el-form-item>
      <el-form-item>
        <el-tooltip
         
          placement="top"
        >
        <div slot="content" > {{radio}} <br>{{tip2}}</div>
          <el-input
            v-model.trim="quanNum"
            ref="ipt3"
            placeholder=""
            clearable
            @keyup.enter.native="$refs.ipt4.focus()"
          ></el-input
        ></el-tooltip>
      </el-form-item>

      <el-form-item>
        <el-input-number
          controls-position="right"
          v-model="money_2"
          placeholder=""
          ref="ipt4"
          :min="0"
          @focus="$refs.ipt4.select()"
          @keyup.enter.native="process_2"
        ></el-input-number>
      </el-form-item>

      <el-form-item>
        <el-button icon="el-icon-sell" @click="process_2" type="primary"
          ></el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import Detail from "./Detail.vue";
  export default {
    name: "ComNum",
    components: { Detail },
    data() {
      return {
        // 
        ymd: "",
        checkedList: [],

        // 
        heNum: "",
        money: 0,
        type: new Map([
          ["", 0],
          ["", 1],
          ["", 2],
          ["", 3],
          ["", 4],
          ["", 5],
        ]),

        // 
        radio: "",
        quanNum: "",
        money_2: 0,

        reg: /^[0-9]{1,}$/,
        details: [],
      };
    },
    created() {
      console.log("QuikHit");
      let { ymd } = this.getTime(new Date());
      this.ymd = ymd;
      // this.details = JSON.parse(localStorage.getItem(this.ymd)).details;
    },
    // activated() {
    //   this.details = JSON.parse(localStorage.getItem(this.ymd)).details;
    // },
    // mounted() {
    //   this.$bus.$on("updateDetails", (details) => {
    //     this.details = details;
    //   });
    // },
    computed: {
      tip() {
        if (this.checkedList.length !== 2) {
          return ''
        }
        let l = this.checkedList[0];
        let r = this.checkedList[1];
        const wei = ["", "", "", ""];
        let w = l + r;
        if (wei.indexOf(l) > wei.indexOf(r)) w = r + l;
        if (w==='') w = "" ;
        if (w == '') w = "";
        return w
      },
      tip2() {
        let hao = `${this.quanNum} X X 0~9`;
        this.radio === "" && (hao = `X ${this.quanNum} X 0~9`);
        this.radio === "" && (hao = `X X ${this.quanNum} : 0~9`);
        this.radio === "" && (hao = ` 0~9 X X ${this.quanNum} `);
        return hao;
      },
    },

    methods: {
      focus() {
        console.log(this.checkedList);
        if (this.checkedList.length === 2) {
          this.$refs.ipt1.focus();
        }
      },

      /**  */
      process() {
        let heNum = this.heNum;
        let money = this.money;

        // 0.
        if (!this.reg.test(heNum)) {
          console.log(heNum.length);
          // this.$message({
          //   type: "error",
          //   message: "",
          //   center: true,
          //   //duration: 1000,
          //   offset: 70,
          // });
          this.$notify({
            
            message: ``,
            type: "error",
            position: "bottom-right",
            offset: 60,
            duration: 3000,
          });
          this.$refs.ipt1.focus();
          return;
        } else if (!money) {
          this.$notify({
            
            message: ``,
            type: "error",
            position: "bottom-right",
            offset: 60,
            duration: 3000,
          });
          this.money = 0;
          return;
        } else if (this.checkedList.length !== 2) {
          this.$notify({
            
            message: ``,
            type: "error",
            position: "bottom-right",
            offset: 60,
            duration: 3000,
          });
          return;
        }

        // 
        /*
          data = {
            type: 0,
            time:'',
            haoMa:'',
            money: 10,
            item: [
              { first: 1, last: 6 },
              { first: 1, last: 5 },
            ],
          };
          */

        const data = {
          type: 0,
          time: "",
          haoMa: "",
          money: 0,
          item: [],
        };

        // 1.X
        let l = this.checkedList[0];
        let r = this.checkedList[1];
        const wei = ["", "", "", ""];
        let w = l + r;
        if (wei.indexOf(l) > wei.indexOf(r)) w = r + l;
        data.type = this.type.get(w);

        console.log(data.type);

        // 2.
        let { hms } = this.getTime(new Date());
        data.time = this.ymd + " " + hms;

        // 3.
        data.haoMa = w + "" + heNum;

        if (data.type === 0) data.haoMa = "" + heNum;
        if (data.type === 3) data.haoMa = "" + heNum;

        // 4.
        data.money = money;

        console.log(data);

        // 5.
        //     
        const arr = Array.from(new Set(heNum.split("")));
        // 
        arr.forEach((value, index, arr) => {
          arr[index] = parseInt(value);
        });

        arr.forEach((value) => {
          for (let i = 0; i < 10; i++) {
            for (let j = 0; j < 10; j++) {
              if ((i + j) % 10 === value) {
                data.item.push({ first: i, last: j });
              }
            }
          }
        });

        console.log(data);

        //  NavBar.vue
        this.$bus.$emit("update", data);

        // this.$message({
        //   showClose: true,
        //   message: ` ${data.haoMa} = ${data.money} = ${
        //     data.item.length * data.money
        //   }`,
        //   type: "success",
        //   center: true,
        //   //duration: 1000,
        //   offset: 70,
        // });

         this.$notify({
          
          message: ` ${data.haoMa} = ${data.money}`,
          type: "success",
          position: "bottom-right",
          offset: 60,
          duration: 3000,
        });

        this.heNum = "";

        // //this.$refs.ipt1.focus();
        this.checkedList = [];

        // // details
        // // this.details = JSON.parse(localStorage.getItem(this.ymd)).details;
      },

      focus_2() {
        // console.log(this.checkedList_2);
        this.$refs.ipt3.focus();
      },

      /**  */
      process_2() {
        let quanNum = this.quanNum;
        let money = this.money_2;

        // 0.
        if (!this.reg.test(quanNum)) {
          // this.$message({
          //   type: "error",
          //   message: "",
          //   center: true,
          //   //duration: 1000,
          //   offset: 70,
          // });
           this.$notify({
            
            message: ``,
            type: "error",
            position: "bottom-right",
            // offset: 60,
            duration: 3000,
          });
          this.$refs.ipt3.focus();
          return;
        } else if (!money) {
         this.$notify({
            
            message: ``,
            type: "error",
            position: "bottom-right",
            // offset: 60,
            duration: 3000,
          });
          
          this.money_2 = 0;
          return;
        } else if (!this.radio) {
           this.$notify({
            
            message: ``,
            type: "error",
            position: "bottom-right",
            offset: 60,
            duration: 3000,
          });

          return;
        }

        // 
        /*
          data = {
            type: 0,
            time:'',
            haoMa:'',
            money: 10,
            item: [
              { first: 1, last: 6 },
              { first: 1, last: 5 },
            ],
          };
          */

        const data = {
          type: 0,
          time: "",
          haoMa: "",
          money: 0,
          item: [],
        };

        // 1.X
        let l = this.radio;
        let w = l + "";

        if (l === "") w = "" + l;

        data.type = this.type.get(w);

        console.log(data.type);

        // 2.
        let { hms } = this.getTime(new Date());
        data.time = this.ymd + " " + hms;

        // 3.
        l === "" && (data.haoMa = `${quanNum}XX`);
        l === "" && (data.haoMa = `X${quanNum}X`);
        l === "" && (data.haoMa = `XX${quanNum}`);
        l === "" && (data.haoMa = `XX${quanNum}`);

        // 4.
        data.money = money;

        console.log(data);

        // 5.
        //     
        const arr = Array.from(new Set(quanNum.split("")));
        // 
        arr.forEach((value, index, arr) => {
          arr[index] = parseInt(value);
        });

        if (l !== "") {
          arr.forEach((value) => {
            for (let i = 0; i < 10; i++) {
              data.item.push({ first: value, last: i });
            }
          });
        } else {
          arr.forEach((value) => {
            for (let i = 0; i < 10; i++) {
              data.item.push({ first: i, last: value });
            }
          });
        }

        console.log(data);

        //  NavBar.vue
        this.$bus.$emit("update", data);

        // this.$message({
        //   showClose: true,
        //   message: ` ${data.haoMa} = ${data.money} = ${
        //     data.item.length * data.money
        //   }`,
        //   type: "success",
        //   center: true,
        //   //duration: 1000,
        //   offset: 70,
        // });
         this.$notify({
         
          message: ` ${data.haoMa} = ${data.money}`,
          type: "success",
          position: "bottom-right",
          // offset: 60,
          duration: 3000,
        });

        this.quanNum = "";

        // //this.$refs.ipt1.focus();
        this.radio = "";

        // // details
        // // this.details = JSON.parse(localStorage.getItem(this.ymd)).details;
      },

      // ,ymd:,hms:
      /**
       * {
       *    ymd:'',
       *    hms:''
       * }
       */
      getTime(date) {
        let month = zeroFill(date.getMonth() + 1); //
        let day = zeroFill(date.getDate()); //
        let hour = zeroFill(date.getHours()); //
        let minute = zeroFill(date.getMinutes()); //
        let second = zeroFill(date.getSeconds()); //

        //
        let hms = hour + ":" + minute + ":" + second;

        let ymd = date.getFullYear() + "-" + month + "-" + day;

        return { ymd: ymd, hms: hms };

        /**
         * 
         */
        function zeroFill(time) {
          if (time >= 0 && time <= 9) {
            return "0" + time;
          } else {
            return time;
          }
        }
      },
    },
  };
</script>

<style lang="css" scoped>
.comnum  .form {
    /* background-color: #ee2c2c; */
    text-align: center;
    padding: 10px;
  }

.comnum  .form  .el-form-item {
    margin: 0 10px;
  }

</style>
